import React, { Component } from "react"
import { Map, TileLayer, Marker, Popup } from "react-leaflet"
import L from 'leaflet'

class MapWithDraw extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPos: {}
    };

  }

  openPopup(marker) {
    if (marker && marker.leafletElement) {
      window.setTimeout(() => {
        marker.leafletElement.openPopup()
      })
    }
  }



  render() {

    const myIcon = L.icon({
      iconUrl: require('../../../../assets/images/map-marker-icon.png'),
      iconSize: [30, 30],
      iconAnchor: [15, 15],
      popupAnchor: [0, -15],
      shadowUrl: null,
      shadowSize: null,
      shadowAnchor: null
    });

    const myIconBlue = L.icon({
      iconUrl: require('../../../../assets/images/map-marker-icon-blue.png'),
      iconSize: [30, 30],
      iconAnchor: [15, 15],
      popupAnchor: [0, -15],
      shadowUrl: null,
      shadowSize: null,
      shadowAnchor: null
    });

    return (
      <div>
        <Map
          ref={m => { this.leafletMap = m; }}
          center={{ lat: 38.079987, lng: 46.296130 }}
          zoom={12}
        // onClick={this.handleClick}
        >
          <TileLayer
            url='https://{s}.tile.osm.org/{z}/{x}/{y}.png'
          // url='https://server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}'
          />

          {this.props.data && this.props.data.monthly && this.props.data.monthly.length > 0 && this.props.data.monthly.map((item, idx) =>
            (item.lat != null && item.lng != null) ?
              <Marker icon={myIconBlue} key={`marker-${idx}`} position={(item.lat != null && item.lng != null) ? [item.lat, item.lng] : '[]'} >
                <Popup>
                  <div className="popupInfo">
                    <div className="itemRow">
                      <span className="title">طرف قرارداد: </span>
                      <span className="content">{item.customer}</span>
                    </div>
                    <div className="itemRow">
                      <span className="title">شماره قرارداد: </span>
                      <span className="content">{item.contract_number}</span>
                    </div>
                    <div className="itemRow">
                      <span className="title">شماره ساختمان: </span>
                      <span className="content">{item.building_number}</span>
                    </div>
                  </div>
                </Popup>
              </Marker>
              : ''
          )}

          {this.props.data && this.props.data.repair && this.props.data.repair.length > 0 && this.props.data.repair.map((item, idx) =>
            (item.lat != null && item.lng != null) ?
              <Marker icon={myIcon} key={`marker-${idx}`} position={[item.lat, item.lng]} >
                <Popup>
                  <div className="popupInfo">
                    <div className="itemRow">
                      <span className="title">طرف قرارداد: </span>
                      <span className="content">{item.customer}</span>
                    </div>
                    <div className="itemRow">
                      <span className="title">شماره قرارداد: </span>
                      <span className="content">{item.contract_number}</span>
                    </div>
                    <div className="itemRow">
                      <span className="title">شماره ساختمان: </span>
                      <span className="content">{item.building_number}</span>
                    </div>
                  </div>
                </Popup>
              </Marker>
              : ''
          )}

        </Map>
      </div>
    )
  }
}

export default MapWithDraw;
